import React from 'react'
import Link from 'gatsby-link'

import Fade from "../components/FadeInWrapper.js"
import styled from 'styled-components'
import breakpoint from "styled-components-breakpoint"

import {PhotoWrapper, TextWrapper, ContentWrapper, PageHeader, Section, SectionHeader } from "../utils/styled-components.js"
import { translate } from "react-i18next"
import RegistrationBar from '../components/RegistrationBar.js'
import Figure from "../components/Figure.js"
import TransitionGroup from "react-transition-group/TransitionGroup"

const PhotoWrapperAbout = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--wCU-GHdb--/c_scale,q_jpegmini,w_3490/v1493993515/kidsmountains_gzskjg.jpg');
`

const TextWrapperAbout = TextWrapper.extend`
  max-width: 960px;
  margin: 0 auto;
`
const AboutWrapper = styled.div`
  position: relative;
`
const PictureWrapper = styled.div`
  padding: 2rem 0;
  display: flex;
  flex-flow: column;
  order: 9;
  ${props => breakpoint('lg', props.theme.breakpoints)`
    min-width: 20rem;
  `}
`
const ImageWrapper = styled.div`
  padding: 2rem 0;
  display: flex;
  flex-flow: column;
`
const Image = styled.img`
  max-height: 20rem;
  margin: .1rem 0;
`
const Portrait = styled.img`
  width: 280px;
  height: 250px;
  margin: .1rem 0;
`
const Caption = styled.label`

`
const AboutSection = Section.extend`
  display: flex;
  flex-flow: column;
  align-items: space-between;

`
const Text = styled.div`
  order: 999;
  ${props => breakpoint('lg', props.theme.breakpoints)`
    width: auto;
  `}
`
const FigureWrapper = styled.div`
  order: 9999999;
`

const AboutPage = ({data, t}) => {
  let dataUri = data[t('about.data')].edges;
  return(
    <AboutWrapper>
      <PhotoWrapperAbout/>
      <PageHeader>{t("about.header")}</PageHeader>
      <ContentWrapper>
        <TextWrapperAbout>
          {
            dataUri.map(({node}, i)=>(
              <AboutSection
              id={node.frontmatter.title.split(' ').join('-')}
              key={i}>
                <SectionHeader>
                  {node.frontmatter.title}
                </SectionHeader>
                <AboutSection>
                { node.frontmatter.picture ?
                    <PictureWrapper>
                      <Portrait src={node.frontmatter.picture} />
                      <Caption>{node.frontmatter.pictureCaption}</Caption>
                    </PictureWrapper>
                    :  null
                  }
                  {
                    node.frontmatter.figure ?
                    <FigureWrapper>
                      <Figure
                      style={{order: 99999999}}
                      images={[...node.frontmatter.figure]} />
                    </FigureWrapper>
                    : console.log("No Figures to Load")
                  }
                  <Text dangerouslySetInnerHTML={{__html: node.html}}></Text>
                </AboutSection>
              </AboutSection>
            ))
          }
        </TextWrapperAbout>
      </ContentWrapper>
      <RegistrationBar/>
    </AboutWrapper>
  )
}

export const query = graphql`
  query AboutQuery {
    en: allMarkdownRemark(
            sort: {fields: [frontmatter___order], order:ASC},
            filter: {fileAbsolutePath: {regex: "/\/home\/bobby\/projects\/Practice\/nslb-gatsby\/src\/data\/about\/en/"}}
          ) {
            edges {
              node {
                frontmatter{
                  title
                  picture
                  pictureCaption
                  figure {
                    url
                    caption
                  }
                }
                html
            }
          }
        },
  es: allMarkdownRemark(
          sort: {fields: [frontmatter___order], order:ASC},
          filter: {fileAbsolutePath: {regex: "/\/home\/bobby\/projects\/Practice\/nslb-gatsby\/src\/data\/about\/es/"}}
        ) {
          edges {
            node {
              frontmatter{
                title
                picture
                pictureCaption
                figure{
                  url
                  caption
                }
              }
              html
          }
        }
      }
}
`

export default translate()(AboutPage)



